<template>
  <v-layout justify-center class="mt-5 pt-5">
    <v-col cols="12" sm="6" md="4">
      <v-card
        v-on:keyup.enter="handleLogin"
        :class="`rounded-xl`"
        dark
        color="#0B0742"
        style="box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;"
      >
        <v-card-text class="text-center">
          <img
            src="https://upload.wikimedia.org/wikipedia/th/thumb/e/e0/RMUTI_KORAT.png/225px-RMUTI_KORAT.png"
            width="80rem"
            alt=""
          />
          <!-- <v-toolbar-title
            class="text-center pa-0"
            style="font-family: 'Yomogi', cursive;"
          >
            สมัครสมาชิก -->
            <!-- <p v-if="user.username" class="ml-2">{{ user.username }}</p> -->
          <!-- </v-toolbar-title> -->
          <v-divider></v-divider>
          <v-form ref="form" v-model="valid" lazy-validation class="ml-5 mr-5">
            <v-text-field
              prepend-icon="mdi-account-circle"
              name="username"
              label="ชื่อผู้ใช้งาน"
              v-model="user.username"
              :rules="[rules.requiredUsers]"
              class="my-input"
            ></v-text-field>
            <v-text-field
              prepend-icon="mdi-lock"
              name="password"
              label="รหัสผ่าน"
              v-model="user.password"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              :rules="[rules.required, rules.requiredPassword]"
              :type="show1 ? 'text' : 'password'"
              @click:append="show1 = !show1"
            ></v-text-field>
            <v-row>
              <v-col>
                <v-text-field
                  v-model="user.firstname"
                  prepend-icon="mdi-rename-box"
                  name="firstname"
                  label="ชือ"
                  :rules="rules.firstname"
                ></v-text-field>
              </v-col>
              <v-col>
                <v-text-field
                  v-model="user.lastname"
                  name="lastname"
                  label="นามสกุล"
                  :rules="rules.lastname"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-text-field
              v-model="user.email"
              prepend-icon="mdi-gmail"
              name="email"
              label="อีเมล"
              :rules="rules.emailMatch"
              required
            ></v-text-field>
            <!-- hint="รหัสผ่านต้องไม่น้อยกว่า 8 ตัว" counter -->
            <v-card-actions class="d-flex justify-space-around">
              <v-btn
                small
                color="purple lighten-1"
                @click="save()"
                :disabled="
                  !valid ||
                    user.username.length < 1 ||
                    user.password.length < 1 ||
                    user.email.length < 1 ||
                    user.firstname.length < 1 ||
                    user.lastname.length < 1
                "
                >สมัครสมาชิก</v-btn
              >
            </v-card-actions>
            <v-card-text>
              หรือ
              <span
                @click="$router.push('/login')"
                style="cursor:pointer;font-size:0.9rem;color:#C4F57E"
                >เข้าสู่ระบบ</span
              >
            </v-card-text>
          </v-form>
        </v-card-text>
      </v-card>
    </v-col>
    <v-snackbar
      :color="colors"
      v-model="snackbar"
      label="Right"
      value="right"
      :timeout="timeout"
      :right="x === 'right'"
      :top="y === 'top'"
      elevation="0"
    >
      <span v-if="error">{{ error }}</span>
      <span v-if="text != ''">{{ text }}</span>

      <template v-slot:action="{ on, attrs }">
        <v-icon
          color="white"
          text
          v-bind="attrs"
          @click="snackbar = false"
          v-on="on"
        >
          mdi-close
        </v-icon>
      </template>
    </v-snackbar>
  </v-layout>
</template>

<script>
// import http from "../http-common";
// import authHeader from "../services/auth-header";
// import User from "../models/user";

export default {
  data() {
    return {
      colors: "",
      valid: true,
      // user: new User("", ""),
      user: {
        username: "",
        password: "",
        email: "",
        firstname: "",
        lastname: "",
      },
      message: "",
      error: false,
      show1: false,
      password: "",
      snackbar: false,
      x: "right",
      y: "top",
      text: "",
      timeout: 2000,
      rules: {
        required: (value) => !!value || "กรอกรหัสผ่าน.",
        emailMatch: [
          (v) => !!v || "กรุณากรอกอีเมล",
          (v) => /.+@.+\..+/.test(v) || "รูปแบบอีเมลไม่ถูกต้อง",
        ],
        requiredUsers: (value) => !!value || "กรอกชื่อผู้ใช้งาน.",
        requiredPassword: (value) => !!value || "กรอกรหัสผ่าน.",
        firstname: [(v) => !!v || "กรุณากรอกชื่อ"],
        lastname: [(v) => !!v || "กรุณากรอกนามสกุล"],
      },
    };
  },
  methods: {
    // login() {
    //   this.$router.push({ path: `/home` });
    // },
    handleLogin() {
      if (this.user.username && this.user.password) {
        this.$store.dispatch("auth/login", this.user).then(
          () => {
            this.$router.push("/home");
          },
          (error) => {
            this.message =
              (error.response && error.response.data) ||
              error.message ||
              error.toString();
            this.snackbar = true;
          }
        );
      }
    },
    save() {
      // this.desserts.push(this.editedItem);
      this.snackbar = false;
      this.$store.dispatch("auth/register", this.user).then(
        () => {
          this.error = "";
          this.colors = "success";
          this.snackbar = true;
          this.text = "สมัครสมาชิกเรียบร้อย";
          this.color = "success";
        },
        (error) => {
          this.snackbar = true;
          this.text = "";
          this.colors = "black";
          this.error =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },
  },

  name: "Login",
  props: {
    source: String,
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  created() {
    if (this.loggedIn) {
      this.$router.push("/home");
    }
  },
};
</script>

<style scope>
.v-application .error--text {
  color: #cf02a3 !important;
}
#user {
  font-size: 10px !important;
}
</style>
